<template>
    <v-container fluid>
            <div class="text-center">
            <h1>Diagnostico Laboratorio Chincha</h1>
            </div>          
                    <v-row justify="center">
                     <v-col  cols="12" sm="6" md="4"  class="text-center" >
                        
                         <v-form ref="formResultadoChincha" lazy-validation @submit.prevent="Enviar" :disabled="cancelarInputResultado">
                          <h4 class="mb-2 red--text text-lighten-4">Las filas rojas son resultados atrasados pendientes</h4>
                          <h4 class="my-2" v-if="pacienteSeleccionado!==''" >{{pacienteSeleccionado}}</h4>
                         <template>
                            <v-file-input
                                label="Subir Archivo"
                                background-color="red lighten-4" 
                                v-model="archivo"
                                outlined
                                dense
                                :rules="archivoRequerido"
                                show-size
                                counter
                                color="red darken-4"
                            ></v-file-input>
                        </template>
                        <v-select :items="arrayUrgencia" 
                                item-color="red darken-4" 
                                class="red--text text-lighten-4" 
                                label="Nivel de Urgencia" 
                                color="red darken-4" 
                                background-color="red lighten-4"  
                                item-text="nombre"
                                item-value="valor"
                                :rules="nivelRequerido"
                                v-model="nivelUrgencia"
                                dense  outlined  ></v-select>
                        <v-textarea  
                         v-model="descripcion" 
                         color="red darken-4"  label="Descripcion" dense outlined 
                         background-color="red lighten-4"
                         :no-resize="true" 
                         ></v-textarea >
                         <div class="d-flex justify-space-between" >
                            <v-btn :disabled="cancelarInputResultado" type="submit" dark color="red darken-4" > Enviar</v-btn>
                         <v-btn  :disabled="cancelarInputResultado"  dark @click="cancelar()" > Cancelar</v-btn>
                         </div>
                         
                          </v-form>
                                <v-alert v-if="modificacionHecha" dense class="mt-4 text-center" type="success">
                                        Registro Exitoso</v-alert>
                     </v-col>
                        <v-col >
                <v-data-table
                        dense
                        fixed-header
                        :dark="true"      
                        :headers="dataHeaders"
                        :items="dataTable"
                        :search="search"
                        class="elevation-1 "
                        :loading="loading"
                        loading-text="Cargando.. Espere porfavor"
                        :item-class="pintarAtrasado"        
                        >
                        
                        <template v-slot:[`item.nombres`]="{ item }">
                                    {{item.nombres}} {{item.apellidos}}
                        </template>
                        <template v-slot:[`item.fechaAtencion`]="{ item }">
                                {{ convertidorFecha(item.fechaAtencion) }}
                        </template>
                        <template  v-slot:[`item.fechaEntregaResultado`]="{ item }" >
                            <td :class="{'red':restarFecha(convertidorFecha(item.fechaEntregaResultado))<-1}">
                             {{convertidorFecha(item.fechaEntregaResultado)}} 
                            </td>
                        </template>     
                         <template  v-slot:[`item.accion`]="{ item }">
                            <v-btn  @click="selectExamen(item)"  elevation="3" icon outlined small class="ma-1">        
                            <v-icon>edit</v-icon>
                            </v-btn>
                        </template>       
                        </v-data-table>

                        </v-col>
                    </v-row>
     <v-dialog v-model="dialogSizeFile" max-width="290" persistent>
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
             El tamaño del archivo excedio los 16 MB
          </div>
        </v-list-item-content>
        <v-list-item-content>
          <v-layout :justify-center="true">          
            <v-btn @click="cerrarDialog()" rounded dark>Cerrar</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
    </v-container>
</template>
<script>
import {mapState} from 'vuex';
export default {
    name:'DiagnosticoChincha',
    data() {
        return {
            loading:true,
            cancelarInputResultado:true,
            modificacionHecha:false,
            pacienteSeleccionado:'',
            mensajeError:'',
            descripcion:'',
            archivo:[],
            nivelUrgencia:'',
            nivelRequerido:[(v)=> !!v || 'Se requiere Nivel'],
            archivoRequerido:[(v)=> (!!v && (v.size>0)) || 'Se requiere Archivo'],
            arrayUrgencia:[{nombre:"Normal",valor:"N"},{nombre:"Media",valor:"M"},{nombre:"Alta",valor:"A"}],
            dataTable:[],
             dataHeaders:[
                 { text:'*', align: 'start', sortable: false, value: 'index', class:"headerStyleLeft red darken-4 subtitle-1 text-center  font-weight-bold white--text " },
                  { text:'Dni', align: 'center', value: 'dni', sortable: false, class:" red darken-4  subtitle-1   font-weight-bold white--text" },
                  { text:'Paciente', align: 'center', sortable: false, value: 'nombres',class:" red darken-4  subtitle-1  font-weight-bold white--text" },
                 { text:'Examen', align: 'center', value: 'examen',  class:" red darken-4  subtitle-1   font-weight-bold white--text" },
                { text:'Atendido', align: 'center', value: 'fechaAtencion', class:" red darken-4  subtitle-1  font-weight-bold white--text" }, 
                 { text:'Entrega Resultado', align: 'center', value: 'fechaEntregaResultado', class:" red darken-4  subtitle-1  font-weight-bold white--text" }, 
                 { text:'',  sortable: false, value: 'accion' ,class:"headerStyleRigtht red darken-4   font-weight-bold white--text" },          
                 ],
            search:'',
            idExamen:'',
            dialogSizeFile:false
        }
    },
    mounted() {
        this.listarExamenes();
    },
    computed: {
        ...mapState(['token'])
    },
    watch:{
            archivo:function(val){
              if(val?.size>1600000){
                this.dialogSizeFile=true;
              }
          },

    },
    methods: {
        pintarAtrasado: function (item) {
     return this.restarFecha(this.convertidorFecha(item.fechaEntregaResultado))<-1 ?'red':'';
     },  
        cerrarDialog(){
            this.dialogSizeFile=false;
            this.archivo=[];
        },
            Enviar(){
                    var regResultado = this.$refs.formResultadoChincha.validate();  
            if(regResultado){
                    this.cancelarInputResultado=true;
                    let formData =new FormData();
                    let filePDF=this.archivo;
                    formData.append("file",filePDF,filePDF.name); 
                    formData.append("idExamen",this.idExamen);  
                    formData.append("fechaInforme",this.FechaActual());  
                    formData.append("horaInforme",this.HoraActual());  
                    formData.append("descripcion",this.descripcion);  
                    formData.append("nivelUrgencia",this.nivelUrgencia); 

                 var headers = {
                    "Content-Type": "application/json",
                    token: this.token,
                };

                 this.axios.post('/guardarResultadoPDF',formData,{headers})
                .then(res=> {
                    this.descripcion='';
                    this.archivo=[];
                    this.nivelUrgencia='';
                    this.modificacionHecha=true;
                    setTimeout(()=>{    this.modificacionHecha=false;},2000)                
                    this.pacienteSeleccionado='';
                     this.dataTable=[];
                    this.listarExamenes();
                })
                 .catch(err=>{
                      this.mensajeError=err;
              setTimeout(()=>{  this.mensajeError=''},2000)
              console.log(err)
                 })
            }
            },       
      FechaActual(){
          const hoy = new Date();
          var año=hoy.getFullYear()
          var mes=(hoy.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=hoy.getDate()
              dia=(dia<10)?('0'+dia):dia;
       return año+'-'+mes+'-'+dia;
      },
        HoraActual(){
          const hoy = new Date();
          var hora=hoy.getHours()
          var min=hoy.getMinutes(); 
          var seg=hoy.getSeconds()
       return hora+':'+min+':'+seg;
      },
      convertidorFecha(str){
            if(str==null){
                return 'Sin Data'
            }else{
                const f = new Date(str);
                var año=f.getFullYear()
                var mes=(f.getMonth() + 1)
                    mes=(mes<10)?('0'+mes):mes;
                var dia=f.getDate()
                    dia=(dia<10)?('0'+dia):dia;
            return año+'/'+mes+'/'+dia;
            }
    },
    restarFecha(e){
       let fecha1 = new Date(e);
        let fecha2 = new Date();
        let resta = fecha1.getTime() - fecha2.getTime()
        return Math.round(resta/ (1000*60*60*24));
    },
      listarExamenes(){
            var headers = {
        "Content-Type": "application/json",
        token: this.token,
       };
          this.axios.get('/listarExamPendientesLabChincha',{headers})
          .then(res=> {
              this.loading=false;
              let index=0;
              this.dataTable=res.data;
                        this.dataTable.map(e=>{
                           
                            e.retraso = this.restarFecha(this.convertidorFecha( e.fechaEntregaResultado))
                        return e})
                    this.dataTable.sort(function (a, b) {
                        if (a.retraso > b.retraso) {
                            return 1;
                        }
                        if (a.retraso < b.retraso) {
                            //como el resultado es menor que 0 
                            //a se coloca en un indice menor es decir primero que el b
                            return -1;
                        }
                        return 0;
                        });
                    this.dataTable.map(e=>{
                        index++;
                        e.index=index;
                         return e})

              })
          .catch(err=>{
            this.loading=false;
            console.log(err) })
           

      },
      selectExamen(e){
          this.pacienteSeleccionado=e.nombres+' '+ e.apellidos+'('+e.examen+')';
          this.idExamen=e.idExamen;
          this.cancelarInputResultado=false;

      },
      cancelar(){
          this.cancelarInputResultado=true;
          this.pacienteSeleccionado='';
          this.idExamen='';
          this.descripcion='';
            this.archivo=[];
            this.nivelUrgencia='';
      }
    },
}
</script>